<template>
  <div id="app">
     <keep-alive>
      <router-view  v-if="$route.meta.keepAlive"></router-view>
     </keep-alive>
      <router-view v-if="!$route.meta.keepAlive"></router-view>
 
  </div>
</template>

<script>
import FingerprintJS from '@fingerprintjs/fingerprintjs';
import {analyticFun} from '@/api/finger'
export default {
  name: 'App',
  data(){
    return {
      
    }
  },
  mounted(){
    this.generateFingerprint();
  },
  methods:{
    async generateFingerprint(){
        // We recommend to call `load` at application startup. monitoring: false
        const options={
            excludes:{enumerateDevices:false}
          } 
        const fp = await FingerprintJS.load(options);  
              // const options={
              //   excludes:{enumerateDevices:false}
              // }  
              // fp.get(options,function(components){
              //     // 参数
              //     const values = components.map(function (component) {
              //         return component.value
              //     });
              //     // 指纹
              //     const murmur = FingerprintJS.x64hash128(values.join(''), 31);
              //     console.log('murmur-----',murmur)
              // })

        const result = await fp.get();
        const visitorId = result.visitorId;
        console.log('visitorId',visitorId);
        const userInfo = JSON.parse(localStorage.getItem('userInfoke'))
        const userId=userInfo && userInfo.id?userInfo.id:''
        analyticFun({userId,fingerId:visitorId}).then(res=>{
          console.log('标识结果：----',res)
        })
    }
  }
}
</script>

<style lang='scss'>
:root{
   --safe-area-inset-bottom: 0px;  
}
@supports (top: env(safe-area-inset-top)) {  
    :root {  
       --safe-area-inset-bottom: constant(safe-area-inset-bottom);  
    }  
}
@supports (top: env(safe-area-inset-top)) {  
    :root {  
        --safe-area-inset-bottom: env(safe-area-inset-bottom);  
    }  
} 
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  //   background: #f5f5f8;
}
</style>



