import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/guideList',
            name: 'guideList',
            component: (resolve) => require(['@/views/guideList'], resolve),
            meta: { title: '索引' }
        },
        {
            path: '/',
            name: 'root',
            redirect: '/guideList',
        },//发布正式环境需注释，防止访问其他页面
        {
            path: '/lineViewsFromRokiApp',
            name: 'lineViewsFromRokiApp',//app分享=>h5----多段烹饪
            component: (resolve) => require(['@/views/lineViewsFromRokiApp'], resolve),
            meta: { title: '多段烹饪', keepAlive: true }
        },
        {
            path: '/login',
            name: 'login',//app分享=>h5----多段烹饪
            component: (resolve) => require(['@/views/webLoginRoki'], resolve),
            meta: { title: 'ROKI登录', keepAlive: false }
        },
        {
            path: '/lotteryDraw',
            name: 'newKitchenDay',
            component: (resolve) => require(['@/views/rokiMarchActivity/newKitchenDay'], resolve),
            meta: { title: '抽奖', keepAlive: true }
        },
        {
            path: '/testPoster',
            name: 'testPoster',
            component: (resolve) => require(['@/views/rokiMarchActivity/newKitchenDay/testcanvas'], resolve),
            meta: { title: 'ceshi', keepAlive: true }
        },
        // {
        //     path: '/lotteryDraw',
        //     name: 'lotteryDraw',
        //     component: (resolve) => require(['@/views/rokiMarchActivity/lotteryDraw'], resolve),
        //     meta: { title: '抽奖', keepAlive: true }
        // },
        {
            path: '/address/:userId',
            name: 'addressEdit',
            component: (resolve) => require(['@/views/rokiMarchActivity/addressEdit'], resolve),
            meta: { title: '收货地址', keepAlive: false }
        },
        {
            path: '/myRewards/:userId',
            name: 'myRewards',
            component: (resolve) => require(['@/views/rokiMarchActivity/myRewards'], resolve),
            meta: { title: '我的奖品', keepAlive: false }
        },
        {
            //cookCulture.vue
            path: '/bannerPages/cookCulture',
            name: 'cookCulture',
            component: (resolve) => require(['@/views/bannerPages/cookCulture'], resolve),
            meta: { title: 'ROBAM老板 | 烹饪文化活动大预告', keepAlive: true }
        },
        {
            path: '/rewardInfo',
            name: 'rewardInfo',
            component: (resolve) => require(['@/views/rewardInfo/index'], resolve),
            meta: { title: '大家来找茬', keepAlive: true }
        },
        //618抽奖活动
        {
            path: '/juneLottery',
            name: 'juneLottery',
            component: (resolve) => require(['@/views/rokiJunActivity/lottery'], resolve),
            meta: { title: '618宠粉抽奖', keepAlive: true }
        },
        {
            path: '/androidTest',
            name: 'androidTest',
            component: (resolve) => require(['@/views/androidTest/test'], resolve),
            meta: { title: '安卓测试'}
        },
    ]
})



