import axios from 'axios';
import { Toast,Dialog } from 'vant';
import {setAccessToken,setRefreshToken,getRefreshToken} from '@/utils/auth';
import {reFreshAccessTokenFun } from '@/api/WebLoginRoki';
import {repeatUseRequestFun} from '@/api/lotteryDraw';
import router from '../router'
axios.defaults.withCredentials = true;
axios.defaults.timeout = 10000;
// 创建axios实例
const current=process.env.NODE_ENV;
export const urlLink = current == "development"||current == "testing"? 'http://develop.h5.myroki.com/distkitch/index.html#' : 'https://h5.myroki.com/distkitch/index.html#';//打包之后指向的地址（正式---oss上）---测试地址为http://develop.h5.myroki.com/distkitch/index.html#/

var baseUrl = '';
var rewardBaseUrl = ''//rewardapi
//var baseUrl='https://api.myroki.com';
// var rewardBaseUrl='https://cook.myroki.com';
 
if(current == "development"||current=='testing'){
    baseUrl='http://develop.api.myroki.com';
    rewardBaseUrl = 'http://develop.manage.myroki.com';
}else{
    baseUrl='https://api.myroki.com';
    rewardBaseUrl='https://cook.myroki.com';
}
export const regularReq = (config) => {
    //返回promise对象
    return new Promise((resolve, reject) => {
        //创建axios实例，把基本的配置放进去
        // let sessionToken = 
        const instance = axios.create({
            //定义请求文件类型
            headers: {
                "Content-Type": 'application/json;charset=UTF-8'
            },
            // timeout: 3000,
            //定义请求根目录
            baseURL: process.env.NODE_ENV == "development" ? '/api' : baseUrl,
        });
        //请求成功后执行的函数
        instance(config).then(res => {
            console.log('request-----res----', res)
            const code = res.data.rc;
            if (code === 0) {
                resolve(res.data);
            } else {
                const errMsg = res.data.msg || res.data.error_description || res.data.error || '未知错误'
                Toast.fail(errMsg)
                reject('error')
            }
        }).catch(err => { //失败后执行的函数
            //console.log(err);
            if(err.response&&err.response.data&&err.response.data.error_description){
                Toast.fail(err.response.data.error_description);
            }else{
                Toast.fail('请求异常');
            }
            //Toast.fail('请求异常');
            reject(err);
        })
    });
}
export const loginReq = (config) => {
    //返回promise对象
    return new Promise((resolve, reject) => {
        //创建axios实例，把基本的配置放进去
        // let sessionToken = 
        const instance = axios.create({
            //定义请求文件类型
            headers: {
                "Content-Type": 'application/json;charset=UTF-8',
                'Cache-Control': 'no-cache'
            },
            // timeout: 3000,
            //定义请求根目录
            baseURL: process.env.NODE_ENV == "development" ? '/api' : baseUrl,
        });
        //请求成功后执行的函数
        instance(config).then(res => {
            console.log('login-res----', res)
            resolve(res.data);
        }).catch(err => {
           // debugger //失败后执行的函数
            //console.log(err.response.data);
            if(err.response&&err.response.data&&err.response.data.error_description){
                Toast.fail(err.response.data.error_description);
            }else{
                Toast.fail('请求异常');
            }
            reject(err);
        })
    });
}
export const rewardRegularReq = (config) => {
    console.log('iiiii-----',localStorage.getItem('accessToken'))
    //返回promise对象
    return new Promise((resolve, reject) => {
        //创建axios实例，把基本的配置放进去
        // let sessionToken = 
        const currentClient=localStorage.getItem('terminal');
        const instance = axios.create({
            //定义请求文件类型
            headers: {
                "Content-Type": 'application/json;charset=UTF-8',
                "clientId":currentClient=='app'?'roki_client':'roki_h5_client'
                //'Cookie':`access_token=${currentClient=='app'?localStorage.getItem('appAccessToken'):localStorage.getItem('webAccessToken')}`
            },
            // timeout: 3000,
            //定义请求根目录
            baseURL: process.env.NODE_ENV == "development" ? '/cook/api-cook-manage' : `${rewardBaseUrl}/api-cook-manage`,
        });
        //console.log('config-----config-----',config)
        //请求成功后执行的函数
        instance(config).then(res => {
            console.log('request-----res----', res)
            const code = res.data.code;
            if (code === 200) {
                resolve(res.data);
            } else if(code === 401){
                //access_token过期之后--重新刷新refresh_token
                if(localStorage.getItem('terminal')!='app'){
                    const refreshToken=getRefreshToken();
                    // var data= new FormData();
                    // data.append('client_id','roki_h5_client');
                    // data.append('client_secret','test');
                    // data.append('refresh_token',refreshToken);
                    const data={
                        'client_id':'roki_h5_client',
                        'client_secret':'test',
                        'refresh_token':refreshToken
                    }
                    reFreshAccessTokenFun(data).then(response=>{
                        if(response.access_token){
                            const date=new Date(new Date().getTime()+response.expires_in*1000)
                            setAccessToken(response.access_token,date);
                            setRefreshToken(response.refresh_token,180);                       
                            repeatUseRequestFun(config).then(r=>{
                                console.log('rrrrrr----',r)
                                resolve(r);
                            }).catch(failobj=>{
                                //Toast.fail('登录过期,请重新登录')
                                Dialog.confirm({
                                    title: '提示',
                                    message: '登录过期,请重新登录',
                                  })
                                .then(() => {
                                    // on confirm
                                    console.log('router',router)
                                    router.push('/login')
                                })
                                reject('error') 
                            })
                        }else{
                            // Toast.fail('登录过期,请重新登录')
                            Dialog.confirm({
                                title: '提示',
                                message: '登录过期,请重新登录',
                              })
                            .then(() => {
                                // on confirm
                                console.log('router',router)
                                router.push('/login')
                            })
                            reject('error')
                        }
                    }).catch(failobj=>{
                        //Toast.fail('登录过期,请重新登录')
                        Dialog.confirm({
                            title: '提示',
                            message: '登录过期,请重新登录',
                          })
                        .then(() => {
                            // on confirm
                            console.log('router',router)
                            router.push('/login')
                        })
                        reject('error')
                    })
                }else{
                    reject('error')
                }
            }else {
                const errMsg = res.data.message||res.data.msg || res.data.error_description || res.data.error || '未知错误'
                Toast.fail(errMsg)
                reject('error')
            }
        }).catch(err => { //失败后执行的函数
            Toast.fail('请求异常');
            reject(err);
        })
    });
}
