import { rewardRegularReq,rewardReqWithoutLogin } from '@/utils/request';
export function lotteryInit(params){
    return rewardRegularReq({
        url: '/cook-manage-admin/api/ops/activity/lottery/userInit',
        method: 'get',
        params
    })
}
export function lotteryTotalPeople(){
    return rewardReqWithoutLogin({
        url: '/cook-manage-admin/api/ops/activity/lottery/statisticsCount',
        method: 'get',
    })
}
export function shareAfterFun(params){
    return rewardRegularReq({
        url: '/cook-manage-admin/api/ops/activity/lottery/share',
        method: 'get',
        params
    })
}
export function addShareUserNote(params){
    return rewardReqWithoutLogin({
        url: '/cook-manage-admin/api/ops/activity/lottery/clickShare',
        method: 'get',
        params
    })
}
export function clickLotteryReward(params){
    return rewardRegularReq({
        url: '/cook-manage-admin/api/ops/activity/lottery/lottery',
        method: 'get',
        params
    })
}
//获取sign
export function getSign(params){
    return rewardRegularReq({
        url: '/cook-manage-admin/api/ops/activity/lottery/getSign',
        method: 'get',
        params
    })
}
export function repeatUseRequestFun(config){
    return rewardRegularReq(config)
}


