import Vue from 'vue'
import {
  Button,
  Image as VanImage,
  Popup,
  Loading,
  Checkbox,
  Toast,
  Skeleton,
  NavBar,
  Dialog,
  Row,
  Col,
  Cell,
  CellGroup,
  Icon,
  Field,
  Form,
  Tabbar,
  Tab,
  Tabs,
  List,
  TabbarItem,
  PullRefresh,
  AddressEdit,
  NoticeBar,
  Swipe,
  SwipeItem,
  CountDown
} from 'vant'
import 'vant/lib/icon/local.css'
Vue.use(Button)
  .use(NavBar)
  .use(VanImage)
  .use(Popup)
  .use(Loading)
  .use(Checkbox)
  .use(Toast)
  .use(Skeleton)
  .use(Dialog)
  .use(Col)
  .use(Row)
  .use(Cell)
  .use(CellGroup)
  .use(Icon)
  .use(Field)
  .use(Form)
  .use(Tabbar)
  .use(Tab)
  .use(Tabs)
  .use(List)
  .use(TabbarItem)
  .use(PullRefresh)
  .use(AddressEdit)
  .use(NoticeBar)
  .use(Swipe)
  .use(SwipeItem)
  .use(CountDown)



